:root {
  --bg: #fdfdfd;
  --fg: #111;
  --muted: #888;
  --border: #e5e5e5;
  --accent: #111;
  --surface: #fff;
}
* {
  box-sizing: border-box;
}
body {
  margin: 0;
  background: var(--bg);
  color: var(--fg);
  font-family: var(--font-body), system-ui, -apple-system, sans-serif;
}
a {
  color: inherit;
  text-decoration: none;
}
.tiptap {
  outline: none;
  min-height: 60vh;
  font-size: 16px;
  line-height: 1.7;
  caret-color: var(--fg);
}
.tiptap p.is-editor-empty:first-child::before {
  content: attr(data-placeholder);
  float: left;
  color: var(--muted);
  pointer-events: none;
  height: 0;
}
.tiptap h1 {
  font-size: 2em;
  font-weight: 700;
  margin: 1em 0 0.4em;
  line-height: 1.2;
}
.tiptap h2 {
  font-size: 1.5em;
  font-weight: 600;
  margin: 0.8em 0 0.3em;
  line-height: 1.3;
}
.tiptap h3 {
  font-size: 1.25em;
  font-weight: 600;
  margin: 0.6em 0 0.2em;
  line-height: 1.4;
}
.tiptap p {
  margin: 0 0 0.6em;
}
.tiptap ul, .tiptap ol {
  padding-left: 1.4em;
  margin: 0 0 0.6em;
}
.tiptap li {
  margin: 0.15em 0;
}
.tiptap blockquote {
  border-left: 3px solid var(--border);
  padding-left: 1em;
  margin: 0.6em 0;
  color: var(--muted);
}
.tiptap code {
  font-family: var(--font-mono), monospace;
  background: #f4f4f4;
  padding: 0.15em 0.35em;
  border-radius: 4px;
  font-size: 0.9em;
}
.tiptap pre {
  background: #f8f8f8;
  border: 1px solid var(--border);
  border-radius: 8px;
  padding: 0.8em 1em;
  margin: 0.6em 0;
  overflow-x: auto;
}
.tiptap pre code {
  background: none;
  padding: 0;
  border-radius: 0;
  font-size: 0.85em;
  line-height: 1.5;
}
.tiptap hr {
  border: none;
  border-top: 1px solid var(--border);
  margin: 1.5em 0;
}
.tiptap strong {
  font-weight: 600;
}
.tiptap em {
  font-style: italic;
}
.papers-loader {
  display: inline-block;
  width: 10px;
  height: 10px;
  border: 1.5px solid var(--border);
  border-top-color: var(--fg);
  border-radius: 50%;
  animation: spin 0.6s linear infinite;
}
@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}
.paper-modal-overlay {
  position: fixed;
  inset: 0;
  z-index: 100;
  background: rgba(0, 0, 0, 0.4);
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 2rem;
  animation: fadeIn 0.15s ease;
}
.paper-modal {
  background: var(--bg);
  border: 1px solid var(--border);
  border-radius: 12px;
  width: 100%;
  max-width: 640px;
  max-height: 80vh;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  animation: slideUp 0.2s ease;
}
.paper-modal-header {
  display: flex;
  align-items: flex-start;
  gap: 0.5rem;
  padding: 1.25rem 1.25rem 0.75rem;
  border-bottom: 1px solid var(--border);
}
.paper-modal-close {
  flex-shrink: 0;
  width: 24px;
  height: 24px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 6px;
  font-size: 12px;
  color: var(--muted);
  cursor: pointer;
  transition: all 0.15s;
  background: none;
  border: none;
}
.paper-modal-close:hover {
  background: #f0f0f0;
  color: var(--fg);
}
.paper-modal-tabs {
  display: flex;
  border-bottom: 1px solid var(--border);
  padding: 0 1.25rem;
}
.paper-modal-tab {
  padding: 0.5rem 0.75rem;
  font-size: 12px;
  color: var(--muted);
  border: none;
  background: none;
  cursor: pointer;
  border-bottom: 2px solid transparent;
  transition: all 0.15s;
  margin-bottom: -1px;
}
.paper-modal-tab:hover {
  color: var(--fg);
}
.paper-modal-tab.active {
  color: var(--fg);
  font-weight: 500;
  border-bottom-color: var(--fg);
}
.paper-modal-body {
  flex: 1;
  overflow: hidden;
  display: flex;
  flex-direction: column;
}
.paper-modal-scroll {
  flex: 1;
  overflow-y: auto;
  padding: 1rem 1.25rem;
}
.paper-chat-msg {
  padding: 0.75rem 0;
}
.paper-chat-msg + .paper-chat-msg {
  border-top: 1px solid var(--border);
}
.paper-chat-msg.assistant .paper-markdown {
  color: var(--fg);
}
.paper-chat-input-bar {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  padding: 0.75rem 1.25rem;
  border-top: 1px solid var(--border);
  background: var(--surface);
}
.paper-markdown code {
  font-family: var(--font-mono), monospace;
  background: #f4f4f4;
  padding: 0.1em 0.3em;
  border-radius: 3px;
  font-size: 0.85em;
}
@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes slideUp {
  from {
    opacity: 0;
    transform: translateY(12px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
.new-doc-option {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  width: 100%;
  padding: 0.875rem 1rem;
  border: 1px solid var(--border);
  border-radius: 8px;
  background: none;
  cursor: pointer;
  transition: all 0.15s;
  text-align: left;
}
.new-doc-option:hover {
  border-color: var(--fg);
  background: #fafafa;
}
.new-doc-icon {
  flex-shrink: 0;
  width: 32px;
  height: 32px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 8px;
  background: #f0f0f0;
  font-size: 14px;
}
.ai-bubble-menu {
  background: var(--bg);
  border: 1px solid var(--border);
  border-radius: 8px;
  box-shadow: 0 4px 16px rgba(0, 0, 0, 0.1);
  padding: 4px;
  animation: fadeIn 0.1s ease;
}
.ai-bubble-actions {
  display: flex;
  flex-wrap: wrap;
  gap: 2px;
}
.ai-bubble-btn {
  display: inline-flex;
  align-items: center;
  gap: 4px;
  padding: 5px 10px;
  font-size: 11px;
  color: var(--fg);
  background: none;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  white-space: nowrap;
  transition: background 0.12s;
}
.ai-bubble-btn:hover {
  background: #f0f0f0;
}
.ai-bubble-loading {
  display: flex;
  align-items: center;
  gap: 6px;
  padding: 6px 12px;
}
